import { useState } from "react";
import { useTranslation } from "react-i18next";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table";
import { Dialog, DialogContent, DialogDescription, DialogFooter, DialogHeader, DialogTitle } from "@/components/ui/dialog";
import { AlertDialog, AlertDialogAction, AlertDialogCancel, AlertDialogContent, AlertDialogDescription, AlertDialogFooter, AlertDialogHeader, AlertDialogTitle } from "@/components/ui/alert-dialog";
import { Plus, Pencil, Trash2, Search, Shield } from "lucide-react";
import { format } from "date-fns";
import {
  useSensitiveWords,
  useAddSensitiveWord,
  useUpdateSensitiveWord,
  useDeleteSensitiveWord,
  SensitiveWord,
} from "@/hooks/useSensitiveWords";

const AdminSensitiveWords = () => {
  const { t } = useTranslation();
  const { data: sensitiveWords, isLoading } = useSensitiveWords();
  const addWord = useAddSensitiveWord();
  const updateWord = useUpdateSensitiveWord();
  const deleteWord = useDeleteSensitiveWord();

  const [searchQuery, setSearchQuery] = useState("");
  const [isAddDialogOpen, setIsAddDialogOpen] = useState(false);
  const [isEditDialogOpen, setIsEditDialogOpen] = useState(false);
  const [isDeleteDialogOpen, setIsDeleteDialogOpen] = useState(false);
  const [newWord, setNewWord] = useState("");
  const [editingWord, setEditingWord] = useState<SensitiveWord | null>(null);
  const [deletingWord, setDeletingWord] = useState<SensitiveWord | null>(null);

  const filteredWords = sensitiveWords?.filter((item) =>
    item.word.toLowerCase().includes(searchQuery.toLowerCase())
  );

  const handleAdd = () => {
    if (!newWord.trim()) return;
    addWord.mutate(newWord, {
      onSuccess: () => {
        setNewWord("");
        setIsAddDialogOpen(false);
      },
    });
  };

  const handleUpdate = () => {
    if (!editingWord || !editingWord.word.trim()) return;
    updateWord.mutate(
      { id: editingWord.id, word: editingWord.word },
      {
        onSuccess: () => {
          setEditingWord(null);
          setIsEditDialogOpen(false);
        },
      }
    );
  };

  const handleDelete = () => {
    if (!deletingWord) return;
    deleteWord.mutate(deletingWord.id, {
      onSuccess: () => {
        setDeletingWord(null);
        setIsDeleteDialogOpen(false);
      },
    });
  };

  return (
    <div className="space-y-6">
      <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4">
        <div>
          <h1 className="text-2xl font-bold flex items-center gap-2">
            <Shield className="h-6 w-6" />
            {t("admin.sensitiveWords.title", "敏感词管理")}
          </h1>
          <p className="text-muted-foreground">
            {t("admin.sensitiveWords.description", "管理评论内容过滤的敏感词列表")}
          </p>
        </div>
        <Button onClick={() => setIsAddDialogOpen(true)}>
          <Plus className="h-4 w-4 mr-2" />
          {t("admin.sensitiveWords.add", "添加敏感词")}
        </Button>
      </div>

      <Card>
        <CardHeader>
          <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4">
            <div>
              <CardTitle>{t("admin.sensitiveWords.list", "敏感词列表")}</CardTitle>
              <CardDescription>
                {t("admin.sensitiveWords.total", "共 {{count}} 个敏感词", {
                  count: sensitiveWords?.length || 0,
                })}
              </CardDescription>
            </div>
            <div className="relative w-full sm:w-64">
              <Search className="absolute left-3 top-1/2 -translate-y-1/2 h-4 w-4 text-muted-foreground" />
              <Input
                placeholder={t("admin.sensitiveWords.search", "搜索敏感词...")}
                value={searchQuery}
                onChange={(e) => setSearchQuery(e.target.value)}
                className="pl-9"
              />
            </div>
          </div>
        </CardHeader>
        <CardContent>
          {isLoading ? (
            <div className="text-center py-8 text-muted-foreground">
              {t("common.loading", "加载中...")}
            </div>
          ) : filteredWords && filteredWords.length > 0 ? (
            <Table>
              <TableHeader>
                <TableRow>
                  <TableHead>{t("admin.sensitiveWords.word", "敏感词")}</TableHead>
                  <TableHead>{t("admin.sensitiveWords.createdAt", "创建时间")}</TableHead>
                  <TableHead className="text-right">{t("common.actions.actions", "操作")}</TableHead>
                </TableRow>
              </TableHeader>
              <TableBody>
                {filteredWords.map((item) => (
                  <TableRow key={item.id}>
                    <TableCell className="font-medium">{item.word}</TableCell>
                    <TableCell>
                      {format(new Date(item.created_at), "yyyy-MM-dd HH:mm")}
                    </TableCell>
                    <TableCell className="text-right">
                      <Button
                        variant="ghost"
                        size="icon"
                        onClick={() => {
                          setEditingWord(item);
                          setIsEditDialogOpen(true);
                        }}
                      >
                        <Pencil className="h-4 w-4" />
                      </Button>
                      <Button
                        variant="ghost"
                        size="icon"
                        onClick={() => {
                          setDeletingWord(item);
                          setIsDeleteDialogOpen(true);
                        }}
                      >
                        <Trash2 className="h-4 w-4 text-destructive" />
                      </Button>
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          ) : (
            <div className="text-center py-8 text-muted-foreground">
              {searchQuery
                ? t("admin.sensitiveWords.noResults", "没有找到匹配的敏感词")
                : t("admin.sensitiveWords.empty", "暂无敏感词")}
            </div>
          )}
        </CardContent>
      </Card>

      {/* Add Dialog */}
      <Dialog open={isAddDialogOpen} onOpenChange={setIsAddDialogOpen}>
        <DialogContent>
          <DialogHeader>
            <DialogTitle>{t("admin.sensitiveWords.add", "添加敏感词")}</DialogTitle>
            <DialogDescription>
              {t("admin.sensitiveWords.addDescription", "添加新的敏感词到过滤列表")}
            </DialogDescription>
          </DialogHeader>
          <Input
            placeholder={t("admin.sensitiveWords.inputPlaceholder", "输入敏感词")}
            value={newWord}
            onChange={(e) => setNewWord(e.target.value)}
            onKeyDown={(e) => e.key === "Enter" && handleAdd()}
          />
          <DialogFooter>
            <Button variant="outline" onClick={() => setIsAddDialogOpen(false)}>
              {t("common.actions.cancel", "取消")}
            </Button>
            <Button onClick={handleAdd} disabled={addWord.isPending || !newWord.trim()}>
              {t("common.actions.add", "添加")}
            </Button>
          </DialogFooter>
        </DialogContent>
      </Dialog>

      {/* Edit Dialog */}
      <Dialog open={isEditDialogOpen} onOpenChange={setIsEditDialogOpen}>
        <DialogContent>
          <DialogHeader>
            <DialogTitle>{t("admin.sensitiveWords.edit", "编辑敏感词")}</DialogTitle>
          </DialogHeader>
          <Input
            placeholder={t("admin.sensitiveWords.inputPlaceholder", "输入敏感词")}
            value={editingWord?.word || ""}
            onChange={(e) =>
              setEditingWord((prev) => (prev ? { ...prev, word: e.target.value } : null))
            }
            onKeyDown={(e) => e.key === "Enter" && handleUpdate()}
          />
          <DialogFooter>
            <Button variant="outline" onClick={() => setIsEditDialogOpen(false)}>
              {t("common.actions.cancel", "取消")}
            </Button>
            <Button onClick={handleUpdate} disabled={updateWord.isPending || !editingWord?.word.trim()}>
              {t("common.actions.save", "保存")}
            </Button>
          </DialogFooter>
        </DialogContent>
      </Dialog>

      {/* Delete Confirmation */}
      <AlertDialog open={isDeleteDialogOpen} onOpenChange={setIsDeleteDialogOpen}>
        <AlertDialogContent>
          <AlertDialogHeader>
            <AlertDialogTitle>{t("admin.sensitiveWords.deleteTitle", "确认删除")}</AlertDialogTitle>
            <AlertDialogDescription>
              {t("admin.sensitiveWords.deleteDescription", "确定要删除敏感词 \"{{word}}\" 吗？", {
                word: deletingWord?.word,
              })}
            </AlertDialogDescription>
          </AlertDialogHeader>
          <AlertDialogFooter>
            <AlertDialogCancel>{t("common.actions.cancel", "取消")}</AlertDialogCancel>
            <AlertDialogAction onClick={handleDelete} className="bg-destructive text-destructive-foreground">
              {t("common.actions.delete", "删除")}
            </AlertDialogAction>
          </AlertDialogFooter>
        </AlertDialogContent>
      </AlertDialog>
    </div>
  );
};

export default AdminSensitiveWords;
